/**
 * Shared Paper.js utilities for converting between Paper.js paths and PathCommand arrays.
 * Used by both PathEditor and PuzzlePaper components.
 */

import type { PathCommand, MoveTo, LineTo, CurveTo } from './types';
import type { PaperContext } from '../utils/paperScope';

/* ========================================================= *\
 *  Paper.js to PathCommand conversion                      *
\* ========================================================= */

/**
 * Convert a Paper.js path to an array of PathCommand objects.
 * Supports MoveTo, LineTo, and CurveTo (bezier) commands.
 *
 * @param path - The Paper.js path to convert
 * @returns Array of PathCommand objects representing the path
 */
export function paperPathToPathCommands(path: paper.Path): PathCommand[] {
  const commands: PathCommand[] = [];

  if (path.segments.length === 0) {
    return commands;
  }

  // First segment is always a MoveTo
  const firstSegment = path.segments[0];
  const moveTo: MoveTo = {
    type: 'move',
    p: [firstSegment.point.x, firstSegment.point.y],
  };
  commands.push(moveTo);

  // Remaining segments
  for (let i = 1; i < path.segments.length; i++) {
    const prevSegment = path.segments[i - 1];
    const segment = path.segments[i];

    // Check if this segment has curve handles
    const hasHandles = (prevSegment.handleOut && prevSegment.handleOut.length > 0)
      || (segment.handleIn && segment.handleIn.length > 0);

    if (hasHandles) {
      // Create a CurveTo command
      // Convert relative handles to absolute control points
      const p1 = prevSegment.handleOut
        ? [prevSegment.point.x + prevSegment.handleOut.x, prevSegment.point.y + prevSegment.handleOut.y]
        : [prevSegment.point.x, prevSegment.point.y];

      const p2 = segment.handleIn
        ? [segment.point.x + segment.handleIn.x, segment.point.y + segment.handleIn.y]
        : [segment.point.x, segment.point.y];

      const curveTo: CurveTo = {
        type: 'bezier',
        p1: p1 as [number, number],
        p2: p2 as [number, number],
        p3: [segment.point.x, segment.point.y],
      };
      commands.push(curveTo);
    } else {
      // Create a LineTo command
      const lineTo: LineTo = {
        type: 'line',
        p: [segment.point.x, segment.point.y],
      };
      commands.push(lineTo);
    }
  }

  // If the path is closed, add a final segment back to the first point
  if (path.closed && path.segments.length >= 2) {
    const lastSegment = path.segments[path.segments.length - 1];
    const firstSegment = path.segments[0];

    // Check if the closing segment has curve handles
    const hasHandles = (lastSegment.handleOut && lastSegment.handleOut.length > 0)
      || (firstSegment.handleIn && firstSegment.handleIn.length > 0);

    if (hasHandles) {
      // Create a CurveTo command to close the path
      const p1 = lastSegment.handleOut
        ? [lastSegment.point.x + lastSegment.handleOut.x, lastSegment.point.y + lastSegment.handleOut.y]
        : [lastSegment.point.x, lastSegment.point.y];

      const p2 = firstSegment.handleIn
        ? [firstSegment.point.x + firstSegment.handleIn.x, firstSegment.point.y + firstSegment.handleIn.y]
        : [firstSegment.point.x, firstSegment.point.y];

      const curveTo: CurveTo = {
        type: 'bezier',
        p1: p1 as [number, number],
        p2: p2 as [number, number],
        p3: [firstSegment.point.x, firstSegment.point.y],
      };
      commands.push(curveTo);
    } else {
      // Create a LineTo command to close the path
      const lineTo: LineTo = {
        type: 'line',
        p: [firstSegment.point.x, firstSegment.point.y],
      };
      commands.push(lineTo);
    }
  }

  return commands;
}

/* ========================================================= *\
 *  PathCommand to Paper.js conversion                      *
\* ========================================================= */

/**
 * Convert an array of PathCommand objects to a Paper.js path.
 * Supports MoveTo, LineTo, and CurveTo (bezier) commands.
 *
 * @param commands - Array of PathCommand objects
 * @param ctx - Paper.js context with isolated scope
 * @returns A new Paper.js path representing the commands
 */
export function pathCommandsToPaperPath(
  commands: PathCommand[],
  ctx: PaperContext
): paper.Path {
  // Scope is already activated by caller
  const paperScope = ctx.scope;
  const path = new paperScope.Path();

  if (commands.length === 0) {
    return path;
  }

  let currentPoint: paper.Point | null = null;

  for (const command of commands) {
    switch (command.type) {
    case 'move': {
      const [x, y] = command.p;
      currentPoint = new paperScope.Point(x, y);
      path.moveTo(currentPoint);
      break;
    }
    case 'line': {
      const [x, y] = command.p;
      currentPoint = new paperScope.Point(x, y);
      path.lineTo(currentPoint);
      break;
    }
    case 'bezier': {
      if (!currentPoint) {
        console.warn('paperUtils: CurveTo command without a current point, skipping');
        break;
      }

      // Convert absolute control points to relative handles
      const [cp1x, cp1y] = command.p1;
      const [cp2x, cp2y] = command.p2;
      const [x, y] = command.p3;

      const endPoint = new paperScope.Point(x, y);
      const cp1 = new paperScope.Point(cp1x, cp1y);
      const cp2 = new paperScope.Point(cp2x, cp2y);

      // Add the new segment
      path.add(endPoint);

      // Get the last segment we just added
      const segment = path.lastSegment;

      // Set handleIn relative to the new point
      const handleIn = cp2.subtract(endPoint);
      segment.handleIn = handleIn;

      // Set handleOut on the previous segment (relative to previous point)
      if (path.segments.length >= 2) {
        const handleOut = cp1.subtract(currentPoint);
        path.segments[path.segments.length - 2].handleOut = handleOut;
      }

      currentPoint = endPoint;
      break;
    }
    case 'arc':
      // Arc commands not yet supported
      console.warn('paperUtils: Arc commands not yet supported, skipping arc command');
      break;
    }
  }

  return path;
}
